.page-header {
  background-color: #1B2D37;
  background-image: url(../../img/bg.png);
  min-height: 850px;
  color: #ffffff;
  padding: 0 50px; }
  .page-header__wrapper {
    max-width: 1410px;
    margin: 0 auto; }
  .page-header__nav-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 170px; }
  .page-header__nav {
    display: flex;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-top: 10px;
    flex-grow: 1; }
    .page-header__nav-item {
      margin-left: 20px;
      padding: 10px; }
  .page-header__content-wrapper {
    display: flex;
    justify-content: space-between; }
  .page-header__headline {
    font-family: "roboto-condensed", "Arial", sans-serif;
    font-weight: bold;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 25px;
    padding-top: 50px; }
  .page-header__desc {
    padding: 0;
    margin: 0;
    position: relative;
    line-height: 34px;
    font-size: 22px;
    margin-left: 100px;
    margin-bottom: 46px; }
    .page-header__desc::before {
      position: absolute;
      content: "";
      top: 20px;
      left: -100px;
      width: 70px;
      height: 2px;
      background-color: #fff; }
  .page-header__main-image {
    margin-right: -70px; }
  .page-header__action {
    margin-left: 100px; }
  .page-header__scroll-down {
    display: inline-block;
    transform: rotate(-90deg) translateY(-40px);
    text-decoration: none;
    color: #ffffff;
    opacity: 0.5;
    font-size: 14px;
    line-height: 16px; }
  .page-header__menu {
    position: relative;
    flex-grow: 1;
    padding-left: 100px;
    cursor: pointer; }
    .page-header__menu::before {
      position: absolute;
      content: "";
      width: 37px;
      height: 1px;
      top: 5px;
      background-color: #ffffff;
      box-shadow: 0 10px 0 0 #ffffff; }
  .page-header__link {
    position: relative;
    text-decoration: none;
    color: inherit; }
    .page-header__link::before {
      position: absolute;
      content: "";
      left: -30px; }
    .page-header__link--tel::before {
      width: 18px;
      height: 18px;
      background-image: url(../../img/phone-call.svg); }
    .page-header__link--mail::before {
      width: 19px;
      height: 14px;
      top: 2px;
      background-image: url(../../img/mail.svg); }
    .page-header__link--mail {
      margin-right: 120px; }

.button {
  display: inline-block;
  padding: 10px 25px;
  border-radius: 5px;
  background-color: transparent;
  text-align: center;
  text-transform: uppercase;
  font-family: "roboto-condensed", "Arial", sans-serif;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px; }
  .button--action {
    background-color: #00B2FF;
    color: #ffffff;
    letter-spacing: 0.05em;
    border: none;
    padding: 18px 50px;
    font-size: 16px;
    line-height: 19px; }
    .button--action:hover {
      background-color: #ebebeb; }
    .button--action:focus {
      background-color: #ebebeb; }
    .button--action:active {
      background-color: #ebebeb; }
  .button--callback {
    border-color: #ffffff;
    border: 1px solid #fff; }
    .button--callback:hover {
      background-color: #5eaa2f; }
    .button--callback:focus {
      background-color: #5eaa2f; }
    .button--callback:active {
      background-color: #5eaa2f;
      color: rgba(255, 255, 255, 0.3); }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

body {
  box-sizing: border-box;
  font-family: "PT-Mono", "Arial", sans-serif; }

*,
*::before,
*::after {
  box-sizing: inherit; }
